@font-face {
  font-family: 'efl_iconfont';
  src: url('../fonts/efl_iconfont.eot?41910875');
  src: url('../fonts/efl_iconfont.eot?41910875#iefix') format('embedded-opentype'),
       url('../fonts/efl_iconfont.woff?41910875') format('woff'),
       url('../fonts/efl_iconfont.ttf?41910875') format('truetype'),
       url('../fonts/efl_iconfont.svg?41910875#efl_iconfont') format('svg');
  font-weight: normal;
  font-style: normal;
}

 [class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "efl_iconfont";
  font-style: normal;
  font-weight: normal;
  speak: none;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */

  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;

  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;

  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;

  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */

  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.icon-font_icons-04:before { content: '\41'; } /* 'A' */
.icon-font_icons-07:before { content: '\44'; } /* 'D' */
.icon-font_icons-01:before { content: '\4c'; } /* 'L' */
.icon-font_icons-02:before { content: '\52'; } /* 'R' */
.icon-font_icons-check:before { content: '\54'; } /* 'T' */
.icon-font_icons-03:before { content: '\58'; } /* 'X' */
.icon-font_icons-05:before { content: '\61'; } /* 'a' */
.icon-font_icons-questionMark:before { content: '\51'; } /* 'Q' */
.icon-font_icons-play:before { content: '\50'; } /* 'P' */
.icon-font_icons-pause:before { content: '\70'; } /* 'p' */
