$efl-font-family: 'proxima_nova_amedium' !default;
$efl-font-base-size: 16px !default;

$mui-base-font-family: $efl-font-family !default;
$mui-base-font-size: $efl-font-base-size !default;

$efl-primary-color: #00AEF3 !default;
$efl-progress-bar-height: 10px !default;
$efl-progress-bar-margin: 5px !default;
$efl-progress-bar-background: #0084C7 !default;
$efl-active-color: #66CC33 !default;
$efl-overlay-background: rgba(255, 255, 255, 0.7) !default;
$efl-show-animation-duration: 0.5s !default;
$efl-slide-down-animation-duration: 0.5s !default;

$efl-content-max-width: 960px !default;
$efl-content-sm-up-horizontal-margin: 30px !default;

$efl-xs-padding: 15px !default;
$efl-sm-padding: 2 * $efl-xs-padding !default;
$efl-md-padding: 3 * $efl-xs-padding !default;
$efl-lg-padding: 4 * $efl-xs-padding !default;
$efl-xl-padding: 5 * $efl-xs-padding !default;

$efl-modal-horizontal-padding: $efl-xl-padding !default;
$efl-modal-vertical-padding: $efl-md-padding !default;
$efl-modal-xs-horizontal-padding: $efl-xs-padding !default;
$efl-modal-xs-vertical-padding: $efl-xs-padding !default;

$efl-spinner-color: $efl-active-color;
$efl-spinner-animation-duration: 2s !default;
$efl-spinner-radius: 92 !default;
$efl-spinner-offset: floor($efl-spinner-radius * 2 * 3.14) - 1;

@import "efl-components";
@import "flexbox";
@import "efl_iconfont";
@import "mark_simonson_-_proxima_nova-webfont";

.efl-journey-root {
    background-color: white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    h1, h2, h3, h4, h5, h6 {
      font-family: $efl-font-family;
  }
}

.mui-container-fluid {
  max-width: $efl-content-max-width + $efl-content-sm-up-horizontal-margin;
  @media (min-width: $mui-screen-sm-min) {
    padding: 0 $efl-content-sm-up-horizontal-margin;
  }
}

.efl-hidden {
  display: none !important;
}

.efl-top-bar {
  position: relative;
  z-index: 99;
  background-color: $efl-primary-color;
  box-shadow: 0px 1px 5px -1px;
  padding: 16px 0;

  h1 {
    text-align: center;
    color: white;
    margin: 0;
    line-height: 1;
  }

  .efl-img-wrapper, .efl-locale-select-wrapper {
    min-width: 150px;
  }

  .efl-locale-select-wrapper {
    display: inline-block;

    .efl-language-picker {
      float: right;

      i {
        font-size: 8px;
      }
    }
  }

  .mui-dropdown {
    button {
      border: 1px solid;
      border-radius: 0;
      background-color: $efl-primary-color;

      span.label {
        padding: 0 4px;
      }

      i {
          position: relative;
          top: -2px;
      }
    }
  }
}

.efl-top-progress-bar-wrapper {
  @extend %flexbox;
  @include flex-direction(row);
  @include align-items(center);

  img, .efl-language-picker {
    @include flex-shrink(0);
    @include flex-grow(0);
  }
}

.efl-progress-bar-wrapper {
  @extend %flexbox;
  @include flex-shrink(0);
  @include flex-grow(1);

  .efl-progress-bar {
    margin: 0 -$efl-progress-bar-margin;

    @media (max-width: $mui-screen-md-min) {
      display: none;
    }
  }
}

.efl-small-progress-bar {
  @include flex-shrink(0);
  @include flex-grow(1);
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: -$efl-progress-bar-margin;
  margin-right: -$efl-progress-bar-margin;

  @media (min-width: $mui-screen-md-min) {
    display: none;
  }
}

.efl-progress-bar, .efl-small-progress-bar {
  @extend %flexbox;
  @include flex-grow(1);
  .efl-module-bar {
    position: relative;
    background: $efl-progress-bar-background;
    margin: 0 $efl-progress-bar-margin;
    @include flex-grow(1);
    height: $efl-progress-bar-height;

    .efl-filling {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 0%;
      background: white;
    }
  }
}

#efl-language-dropdown-list {
    z-index: 100;
}

.efl-player {
  @include flex-shrink(0);
  @include flex-grow(1);

  iframe {
    width: 100%;
  }
}

.efl-footer {
  position: relative;
  background-color: #DDE2E8;
  min-height: 80px;
  width: 100%;

  .mui-container-fluid {
    position: relative;
  }

  button {
    position: absolute;
    top: -43px;
    padding-top: 4px;

    &.efl-left {
      left: 16px;
      @media (min-width: $mui-screen-sm-min) {
        left: $efl-content-sm-up-horizontal-margin;
      }
    }

    &.efl-right {
      right: 16px;
      @media (min-width: $mui-screen-sm-min) {
        right: $efl-content-sm-up-horizontal-margin;
      }
    }

    i {
        font-size: 50px;
        margin-left: -23px;
    }
  }
}

#efl-title-timer-container {
    @media (min-width: $mui-screen-sm-min) {
        @extend %flexbox;
    }

    @include align-items(center);
    @include flex-wrap(wrap);

    @media (min-width: $mui-screen-md-min) {
        margin-top: 10px;
    }
}

#efl-title {
    @include flex-shrink(0);
    @include flex-grow(1);
}

#efl-timer {
    @extend %flexbox;
    background-color: #DDE2E8;
    padding: 6px 16px 4px 16px;
    width: 150px;
    border-radius: 1000px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.2), 0 3px 3px rgba(0, 0, 0, 0.3);
    @include justify-content(space-between);
    @include align-items(center);
    margin: 0 auto;
}

#efl-timer-container {
    @extend %flexbox;
    @include justify-content(center);

    @media (max-width: $mui-screen-xs-max) {
        margin-top: 10px;
        width: 100%;
    }

    @media (min-width: $mui-screen-sm-min) {
        margin-left: auto;
    }
    z-index: 99;
}

#efl-timer-time {
    @include flex-shrink(0);
    @include flex-grow(1);
    text-align: center;
    font-size: 24px;
    color: #969899;
}

#efl-timer-clock {
    position: relative;
    top: -2px;
    width: 22px;
    height: 22px;
    background: #FFFFFF;
    border-radius: 50%;
    display: inline-block;
}

#efl-timer-button {
    color: $efl-active-color;
    font-size: 23px;
    margin: 0;
    padding: 0;
    border-style: none;
    background-color: transparent;

    &:focus {
        outline-style: none;
    }

    [class^="icon-"]:before  {
        margin-left: 0;
        margin-right: 0;
    }
}

#efl-timer-svg {
    z-index: 10;
    display: block;
}

#efl-timer-path {
    fill: $efl-active-color;
}

.efl-overlay {
    background-color: $efl-overlay-background;
}

#efl-below-nav-overlay {
    z-index: 90;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
}

#efl-nav-overlay {
    z-index: 90;
    position: absolute;
    top: 0;
    right: 0;
    bottom: -4px;
    left: 0;
    opacity: 0;
}

@keyframes efl-show-animation-def {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes efl-hide-animation-def {
    from {opacity: 1;}
    to {opacity: 0;}
}

.efl-show-animation {
    animation-name: efl-show-animation-def;
    animation-duration: $efl-show-animation-duration;
    animation-fill-mode: forwards;
}

.efl-hide-animation {
    animation-name: efl-hide-animation-def;
    animation-duration: $efl-show-animation-duration;
}

#efl-global-overlay {
    background-color: $efl-overlay-background;
    z-index: 200;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.efl-modal {
    background-color: #FFFFFF;
    border: solid 2px $efl-primary-color;
    border-radius: 6px;
    margin-top: 100px;
    padding: $efl-modal-vertical-padding $efl-modal-horizontal-padding;
    margin-left: auto;
    margin-right: auto;
    position: relative;

    h2 {
        margin: 0 0 $efl-sm-padding 0;
    }

    > div {
        margin-bottom: $efl-sm-padding;
    }

    > div:last-child {
        margin-bottom: 0;
    }

    @media (max-width: $mui-screen-xs-max) {
        margin-top: 40px;
        padding: $efl-modal-xs-vertical-padding $efl-modal-xs-horizontal-padding;

        h2 {
            margin-bottom: $efl-xs-padding;
            text-align: center;
        }

        div {
            margin-bottom: $efl-xs-padding;
        }
    }
}

#efl-time-extension-modal {
    max-width: 730px;
    button {
        width: 190px;
    }

    form {
        margin-bottom: $efl-md-padding;

        @media (max-width: $mui-screen-xs-max) {
            margin-bottom: $efl-sm-padding;
        }

        div {
            text-align: center;
            margin-bottom: $efl-xs-padding;

            @media (max-width: $mui-screen-xs-max) {
                text-align: left;
            }
        }

        .efl-radio {
            margin-right: auto;
            margin-left: auto;

            @media (max-width: $mui-screen-xs-max) {
                width: 100%;
            }
        }
    }
}

#efl-time-extension-cancel {
    text-align: left;
    position: relative;

    @media (max-width: $mui-screen-xs-max) {
        text-align: center;
    }
}

#efl-time-extension-ok {
    text-align: right;
    position: relative;

    @media (max-width: $mui-screen-xs-max) {
        text-align: center;
    }
}

@keyframes slide-down-animation-def {
    from {bottom: 150vh; opacity: 1}
    to {bottom: 0; opacity: 1}
}

.efl-modal-slide-down-animation {
    animation-name: slide-down-animation-def;
    animation-duration: $efl-slide-down-animation-duration;
    animation-fill-mode: forwards;
    animation-delay: $efl-show-animation-duration * 0.5;
}

.efl-buttons-row {
    display: flex;
    align-items: center;
    flex-wrap: wrap-reverse;
}

.efl-spinner {
    animation: efl-rotator $efl-spinner-animation-duration linear infinite;

    circle {
        stroke-dasharray: $efl-spinner-offset;
        stroke-dashoffset: 0;
        transform-origin: center;
        animation: efl-dash $efl-spinner-animation-duration ease-in-out infinite;
        stroke: $efl-spinner-color;
    }
}

@keyframes efl-rotator {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(270deg); }
}

@keyframes efl-dash {
    0% { stroke-dashoffset: $efl-spinner-offset; }
    50% {
        stroke-dashoffset: $efl-spinner-offset / 4;
        transform:rotate(135deg);
    }
    100% {
        stroke-dashoffset: $efl-spinner-offset;
        transform:rotate(450deg);
    }
}

.efl-loading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    text-align: center;

    > div {
        margin-top: $efl-sm-padding;
    }
}
